import FlexWrapper from "./FlexWrapper";
import { Box, Heading, Text } from "@mechatronick/datum-design-system";
import React from "react";
import Fade from "react-reveal/Fade";

const MultipleImageAndTextCombo = props => {
  const { data } = props;
  return (
    <>
      <FlexWrapper
        style={{
          textAlign: "center",
        }}
        wrap={"wrap"}
      >
        {data.map((eachProperty, index) => {
          return (
            <Box
              key={eachProperty.title}
              width={[1 / 2, 1 / 3, 1 / 4]}
              p={[2, 2, 3]}
            >
              <Box py={[2, 3, 4]}>
                <Fade bottom>
                  <img
                    title={eachProperty.title}
                    alt={eachProperty.title}
                    width={"auto"}
                    height={"100px"}
                    src={eachProperty.svg}
                  />
                </Fade>
              </Box>
              <Fade delay={300}>
              <Heading.h4 bold>{eachProperty.title}</Heading.h4>
              <Text.p>{eachProperty.description}</Text.p>
              </Fade>
            </Box>
          );
        })}
      </FlexWrapper>
    </>
  );
};

export default MultipleImageAndTextCombo;
