import {graphql, useStaticQuery} from "gatsby"

export const useMemberPicsHook = () => (
    useStaticQuery(
        graphql`
            fragment squareImage200 on File {
                childImageSharp {
                    fluid(maxWidth: 200, maxHeight: 200) {
                        ...GatsbyImageSharpFluid
                    }
                }
            }
            
            query {
                NghiaNguyen: file(relativePath: { eq: "team/NghiaNguyen.jpeg" }) {
                    ...squareImage200
                }
                BacNguyen: file(relativePath: { eq: "team/default.png" }) {
                    ...squareImage200
                }
                ThangNguyen: file(relativePath: { eq: "team/default.png" }) {
                    ...squareImage200
                }
                TanDuong: file(relativePath: { eq: "team/default.png" }) {
                    ...squareImage200
                }
                ThinhPhan: file(relativePath: { eq: "team/ThinhPhan.png" }) {
                    ...squareImage200
                }
                NhanNguyen: file(relativePath: { eq: "team/NhanNguyen.png" }) {
                    ...squareImage200
                }
                NickDo: file(relativePath: { eq: "team/NickDo.png" }) {
                    ...squareImage200
                }
            }
        `
    )
);