import React, {Component} from "react";
import {Box, Input, Text, theme} from "@mechatronick/datum-design-system"
import FlexWrapper from "../FlexWrapper"
import ButtonWithNoLink from "../ButtonNoLink"
import jsonp from "jsonp";
import toQueryString from "to-querystring";
import helperLib from "../../helperLib";

const getAjaxUrl = url => url.replace("/post?", "/post-json?");

const SUBSCRIBE_STATUS = {
    NULL: null,
    SENDING: "SENDING",
    ERROR: "ERROR",
    SUCCESS: "SUCCESS"
};

class SubscribeForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: "",
            status: SUBSCRIBE_STATUS.NULL,
            message: null
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        const {value} = event.target;
        this.setState({
            email: value
        })
    }

    handleSubmit() {
        const {email} = this.state;
        const data = {EMAIL: email};
        if (helperLib.validEmail(this.state.email)) {
            const params = toQueryString(data);
            const url = getAjaxUrl(this.props.url) + "&" + params;
            this.setState(
                {
                    status: SUBSCRIBE_STATUS.SENDING
                },
                () =>
                    jsonp(
                        url,
                        {
                            param: "c"
                        },
                        (err, data) => {
                            if (err) {
                                this.setState({
                                    status: SUBSCRIBE_STATUS.ERROR,
                                    message: err
                                });
                            } else if (data.result !== "success") {
                                this.setState({
                                    status: SUBSCRIBE_STATUS.ERROR,
                                    message: data.msg
                                });
                            } else {
                                this.setState({
                                    status: SUBSCRIBE_STATUS.SUCCESS,
                                    message: data.msg
                                });
                            }
                        }
                    )
            );
        } else {
            this.setState(
                {
                    status: SUBSCRIBE_STATUS.ERROR
                }
            );
        }
    }

    render() {
        const {status} = this.state;
        return (
            <>
                <Box
                    width={[1, 1 / 5, 3 / 8]}
                    p={[2, 0, 2]}
                >
                    <Input
                        type={"email"}
                        name={"email"}
                        placeholder={"hello@business.com"}
                        onChange={this.handleChange}
                    />
                </Box>
                {status === SUBSCRIBE_STATUS.NULL && (
                    <Box
                        width={[1, 2 / 5, 1 / 8]}
                        p={[2, 0, 2]}
                    >
                        <ButtonWithNoLink
                            p={2}
                            m={0}
                            onClick={this.handleSubmit}
                        >
                            SUBSCRIBE
                        </ButtonWithNoLink>
                    </Box>
                )}
                {status === SUBSCRIBE_STATUS.SENDING && (
                    <Box
                        width={[1, 2 / 5, 1 / 8]}
                        p={[2, 0, 2]}
                    >
                        <ButtonWithNoLink
                            p={2}
                            m={0}
                            color={"lightAccent"}
                        >
                            LOADING
                        </ButtonWithNoLink>
                    </Box>
                )}
                {status === SUBSCRIBE_STATUS.ERROR && (
                    <Box
                        width={[1, 2 / 5, 1 / 8]}
                        p={[2, 0, 2]}
                    >
                        <ButtonWithNoLink
                            p={2}
                            m={0}
                            color={"darkAccent"}
                            onClick={this.handleSubmit}
                        >
                            ERROR! TRY AGAIN
                        </ButtonWithNoLink>
                    </Box>
                )}
                {status === SUBSCRIBE_STATUS.SUCCESS && (
                    <Box
                        width={[1, 2 / 5, 1 / 8]}
                        p={[2, 0, 2]}
                    >
                        <ButtonWithNoLink
                            p={2}
                            m={0}
                            color={"successGreen"}
                            disable
                        >
                            THANK YOU
                        </ButtonWithNoLink>
                    </Box>
                )}
            </>
        );
    }
}

export default () => (
    <>
        <div
            style={{backgroundColor: theme.colors["primary"], boxShadow: theme.boxShadows[2]}}
        >
            <FlexWrapper
                style={{
                    textAlign: "center"
                }}
                wrap={"wrap"}
                alignItems={"center"}
            >
                <Box
                    width={[1, 2 / 5, 1 / 2]}
                    p={[2, 2, 3]}
                >
                    <Text
                        f={[1, 2, 3]}
                        color={"white"}
                    >
                        Join our newsletter list of innovators and receive an email update every other month
                    </Text>
                </Box>
                <SubscribeForm
                    url={"https://datumhq.us16.list-manage.com/subscribe/post?u=6183d6ff23a8757543f31d449&amp;id=17062ce8dc"}
                />
            </FlexWrapper>
        </div>
    </>
);