import { Box, Text, theme } from "@mechatronick/datum-design-system";
import React from "react";
import PropsTypes from "prop-types";

const Member = ({ alt, src, textMaxWidth, name, position }) => (
  <div
    style={{
      textAlign: "center",
    }}
  >
    <img
      title={alt}
      alt={alt}
      src={src}
      width={"auto"}
      height={"200px"}
      style={{
        boxShadow: `${theme.boxShadows[2]}`,
        zIndex: 1,
        position: "relative",
      }}
    />
    <Box
      style={{
        maxWidth: textMaxWidth,
        margin: "-30px auto 30px auto",
        zIndex: 3,
        backgroundColor: "white",
        position: "relative",
        border: `4px solid ${theme.colors["accent"]}`,
        boxShadow: `${theme.boxShadows[1]}`,
      }}
      px={2}
      py={1}
      f={1}
    >
      <Text color={"accent"} bold>
        {name}
      </Text>
      <Text color={"accent"}>{position}</Text>
    </Box>
  </div>
);

Member.propTypes = {
  alt: PropsTypes.string.isRequired,
  src: PropsTypes.string.isRequired,
  textMaxWidth: PropsTypes.string,
  name: PropsTypes.string.isRequired,
  position: PropsTypes.string.isRequired,
};

export default Member;
